import React, {useContext, useState, useEffect} from "react";
import Logo from '../../../assets/Logo.svg';
import Mail from '../../../assets/Mail.svg';
import Lock from '../../../assets/Lock.svg';
import { AuthContext } from "../../../contexts/authContext";
interface IProps {
}

export const Login : React.FC < IProps > = ({
    ...props
}) => {
    const {loginError, status, dispatchLogin, emailOnChange, passwordOnChange} = useContext(AuthContext);
    const [loginStatus, setLoginStatus] = useState(loginError);
    const handleEmailChange = (e : any) => emailOnChange(e.target.value);
    const handlePasswordChange = (e : any) => passwordOnChange(e.target.value);
    const handleSubmitForm = (e : any) => {
        e.preventDefault();
        dispatchLogin();
    };

    
    useEffect(()=>{
        setLoginStatus(loginError);
    }, [status, loginError]);

    return (
        <div className="login_page">
            <div className="login_container">

                <img className="logo" src={Logo} alt=""/>

                <form onSubmit={handleSubmitForm}>
                    <span className="title">Sign In</span>
                    <a href="#" className="forget_link">Forgot password?</a>
                    <div className="form-group">
                        <div className="input-group">
                            <img className="input_icon" src={Mail} alt=""/>
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Email"
                                onChange={handleEmailChange}
                                required/>
                        </div>
                    </div>

                    <div className="form-group">
                        <div className="input-group">
                            <img className="input_icon" src={Lock} alt=""/>
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Password"
                                onChange={handlePasswordChange}
                                required/>
                        </div>
                    </div>
                    {status === 'error' ? (
                    <div className="form-group alert alert-danger">
                        {loginStatus}
                    </div>
                    ) : ''}
                    <button type="submit" className="btn btn-primary btn-block submit">Sign in</button>
                    <div className="join_link">
                        <a href="#">Join COREvoyant</a>
                    </div>

                </form>
            </div>
        </div>

    );
};