import * as React from "react";
import list from "../../assets/list.svg";
import clockcircle from "../../assets/clock-circular-outline.svg";
import bubbletext from "../../assets/speech-bubble-with-text-lines.svg";
import notification from "../../assets/notification-small.svg";
import CardDetails from "./CardDetails";
import { useState } from "react";
import history from "../../utils/history";
import { AppContext } from "contexts/appContext";

export function Card(prop) {
  // console.log(prop);
  const isEmpty = prop.isEmpty;
  const {
    currentCardId,
    currentBoard,
    boards,
    setCurrentBoardId
  } = React.useContext(AppContext);

  const [carddetailsShow, setcarddetailsShow] = useState(false);

  const handlecarddetailsClose = () => {
    setcarddetailsShow(false);
    setCurrentBoardId(undefined);
    history.push(`/boarddetails/${currentBoard.id}`);
  };

  const handlecarddetailsShow = () => setcarddetailsShow(true);

  const onCardClick = () => {
    handlecarddetailsShow();
    history.push(`/boarddetails/${currentBoard.id}/${prop.id}`);
  };

  React.useEffect(() => {
    if (currentCardId === prop.id && boards.length) {
      console.log("currentCardId", currentCardId);

      handlecarddetailsShow();
    }
  }, [currentCardId, boards]);

  const cardDetails = {
    cardTitle: prop.title,
    description: prop.description,
    otherData: {},
    label: prop.label,
    filesToUpload: prop.numberOfFilesUploaded
  };

  try {
    cardDetails.otherData = JSON.parse(decodeURIComponent(prop.otherData));
  } catch (error) {
    console.log(error);
  }

  if (typeof cardDetails.otherData !== typeof {}) {
    cardDetails.otherData = {};
  } else if (cardDetails.otherData === null) {
    cardDetails.otherData = {};
  }
  //   console.log(cardDetails.otherData);
  return (
    <>
      {isEmpty === undefined ? (
        <>
          <div className="card_item" onClick={onCardClick}>
            <div className="card_title">{prop.title}</div>
            <div className="typeofloss">
              <div className="title">Type of Loss</div>
              <ul>
                {cardDetails.otherData["Loss Type"] ? (
                  cardDetails.otherData["Loss Type"].map((loss, index) => {
                    return (
                      <li title={loss} key={index}>
                        {loss}
                      </li>
                    );
                  })
                ) : (
                  <li className="notype">Not Available</li>
                )}
              </ul>
            </div>
            <div className="claimno">
              <div className="title">Claim #</div>
              <div className="value">
                {cardDetails.otherData["Claim #"]
                  ? cardDetails.otherData["Claim #"]
                  : "Not Available"}
              </div>
            </div>
            <div className="footer_info">
              {/* <div className="footer_icons">
                        <img src={clockcircle} alt="" /> 1 Jan 2020
                    </div> */}
              <div className="footer_icons">
                <img src={bubbletext} alt="" /> 0
              </div>
              <div className="footer_icons redalert">
                <img src={notification} alt="" /> 1
              </div>
            </div>
          </div>
          <CardDetails
            handleClose={handlecarddetailsClose}
            show={carddetailsShow}
            data={cardDetails}
          ></CardDetails>
        </>
      ) : (
        <div className="no_lane_card">No Record Found</div>
      )}
    </>
  );
}
