import React, { useEffect } from "react";
import { withRouter } from "react-router-dom";

import Header from "../../components/header";
import BoardHeader from "../../components/boardHeader";
import { BoardView } from "../../components/BoardView";
import { AppContext } from "../../../contexts/appContext";
import { useContext } from "react";
import NoBoardFound from "app/components/NoBoardFound";
import FromsSidebar from "app/components/FromsSidebar";
import FormsView from "app/components/FormsView";

export const Forms: React.FC = props => {
  const {
    setCurrentBoardId,
    dispatchGetBoardDetails,
    currentBoard
  } = useContext(AppContext);

  if (props.match.params.boardid) {
    setCurrentBoardId(props.match.params.boardid);
  }

  useEffect(() => {
    dispatchGetBoardDetails();
  }, []);

  return (
    <>
      <Header></Header>
      {currentBoard && currentBoard.data.lanes.length ? (
        <>
          <BoardHeader isHideRightSection={true} view="forms"></BoardHeader>
          <div className="row no-gutters">
              <div className="col-3 forms_sidebar">
                  <FromsSidebar></FromsSidebar>
              </div>
              <div className="col-9">
                  <FormsView></FormsView>
              </div>
          </div>
        </>
      ) : (
        <NoBoardFound></NoBoardFound>
      )}
    </>
  );
};

export default withRouter(Forms);
