import * as React from "react";
import menubanner from "../../assets/menubanner.svg";
import plus from "../../assets/plus.svg";
import { Dropdown, Modal, Button } from "react-bootstrap";
import { useState } from "react";
import { AppContext } from "../../contexts/appContext";
const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={e => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
  </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const CustomMenu = React.forwardRef(
  ({ children, style, className, "aria-labelledby": labeledBy }, ref) => {
    const [value, setValue] = useState("");

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">
          {React.Children.toArray(children).filter(
            child =>
              !value || child.props.children.toLowerCase().startsWith(value)
          )}
        </ul>
      </div>
    );
  }
);

const RenameStack = props => {
  return (
    <>
      <Modal
        show={props.show}
        onHide={props.handleClose}
        className="contect_support_model"
      >
        <Modal.Header closeButton>
          <Modal.Title className="contact_support_model_title">
            Rename Stack
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="support_body">
          <form onSubmit={props.onStackSubmit}>
            <div className="form-group">
              <input
                type="text"
                placeholder="Name"
                value={props.value}
                onChange={props.onChange}
                required
              />
            </div>
            <Button
              variant="primary"
              className="btn send_support"
              type="submit"
            >
              Change
            </Button>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

const CreateCard = props => {
  return (
    <>
      <Modal
        show={props.show}
        onHide={props.handleClose}
        className="contect_support_model"
      >
        <Modal.Header closeButton>
          <Modal.Title className="contact_support_model_title">
            Create New Card
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="support_body">
          <form onSubmit={props.onCardSubmit}>
            <div className="form-group">
              <input
                type="text"
                placeholder="Name"
                value={props.value}
                onChange={props.onChange}
                required
              />
            </div>
            <Button
              variant="primary"
              className="btn send_support"
              type="submit"
            >
              Create
            </Button>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export function LaneHeader(prop) {
  const [stackShow, setStackShow] = useState(false);
  const { renameBoardLane, addCartToLane } = React.useContext(AppContext);
  const handleStackClose = () => setStackShow(false);
  const handleStackShow = () => setStackShow(true);
  const [currentStack, setCurrentStack] = useState({
    name: prop.title
  });
  const [currentCard, setCurrentCard] = useState({
    name: ""
  });
  const [cardShow, setCardShow] = useState(false);

  const handleCardClose = () => setCardShow(false);
  const handleCardShow = () => setCardShow(true);

  const onChange = e => {
    const value = e.currentTarget.value;
    setCurrentStack({ name: value });
  };
  const onChangeCard = e => {
    const value = e.currentTarget.value;
    setCurrentCard({ name: value });
  };

  const onStackSubmit = e => {
    e.preventDefault();
    renameBoardLane({
      title: currentStack.name,
      id: prop.id
    });
    handleStackClose();
  };

  const onCardSubmit = e => {
    e.preventDefault();
    addCartToLane({
      title: currentCard.name,
      id: prop.id
    });
    handleCardClose();
    setCurrentCard({name: ''});
  };

  return (
    <div className="lane_header">
      <div className="row">
        <div className="col">{prop.title}</div>

        <div className="col text-right">
          <Dropdown>
            <Dropdown.Toggle as={CustomToggle} id="dropdown-alerts">
              <img src={menubanner} alt="" />
            </Dropdown.Toggle>

            <Dropdown.Menu
              as={CustomMenu}
              alignRight="true"
              className="create_card_dropdown_menu"
            >
              <li>
                <Dropdown.Item
                  eventKey="1"
                  href="#"
                  onClick={() => {
                    handleCardShow(true);
                  }}
                >
                  <img src={plus} alt="" /> <span>Create Card</span>
                </Dropdown.Item>
              </li>
              <li>
                <Dropdown.Item
                  eventKey="1"
                  onClick={() => {
                    handleStackShow(true);
                  }}
                >
                  <img src={plus} alt="" /> <span>Rename Stack</span>
                </Dropdown.Item>
              </li>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
      <RenameStack
        value={currentStack.name}
        onChange={onChange}
        onStackSubmit={onStackSubmit}
        show={stackShow}
        handleClose={handleStackClose}
      ></RenameStack>

      <CreateCard
        value={currentCard.name}
        onChange={onChangeCard}
        onCardSubmit={onCardSubmit}
        show={cardShow}
        handleClose={handleCardClose}
      ></CreateCard>
    </div>
  );
}
