import * as React from 'react';
import plus from '../../assets/plus_lane.svg';
import { Modal, Button } from 'react-bootstrap';
import { useContext } from 'react';
import { AppContext } from 'contexts/appContext';


const StackModal = (props) => {
    const {
        addBoardLane
      } = useContext(AppContext);
      let newLaneName = '';
      

      const addLane = (e) => {
          e.preventDefault();
          addBoardLane({
              title: newLaneName
          });
          props.handleClose();
      }
    return (
      <>
        <Modal show={props.show} onHide={props.handleClose} className="stack_model">
          <Modal.Header closeButton>
            <Modal.Title className="stack_model_title"> Add New Stack</Modal.Title>
          </Modal.Header>
          <Modal.Body className="stack_body">
              <form onSubmit={addLane}>
                <div className="form-group">
                    <input type="text" placeholder="Name" required onChange={(e) => {newLaneName = e.currentTarget.value;}} />
                </div>
                <Button variant="primary" className="btn send_stack" type="submit">
                    Add
                </Button>
              </form>
          </Modal.Body>
        </Modal>
      </>
    );
  }


export function CustomAddLaneButton(prop) {
    const [stackShow, setstackShow] = React.useState(false);

    const handleStackClose = () => setstackShow(false);
    const handleStackShow = () => setstackShow(true);
  
    return (
        <div className="">
          <button className="add_lane_button"
          onClick={handleStackShow}
          >
              <img src={plus} alt="" />
              Add New Stack
          </button>
          <StackModal handleClose={handleStackClose} show={stackShow} ></StackModal>
        </div>
    );
}
