import * as React from 'react';
import Board from 'react-trello';
import { Card } from './card';
import { LaneHeader } from './LaneHeader';
import { CustomAddCardLink } from './CustomAddCardLink';
import { AppContext } from '../../contexts/appContext';
import { useContext } from 'react';
import { CustomAddLaneButton } from './CustomAddLaneButton';
export function BoardView(match) {
  const {
    dispatchGetBoardDetails,
    currentBoard,
    dispatchUpdateBoardDetails,
    onBoardDataChange,
    setBoardEventBus,
  } = useContext(AppContext);
  const [boardData, setBoardData] = React.useState({});
  const formatBoardData = (boardData) => {
    boardData.lanes.map((lane) => {
      if(!lane.cards.length){
        // lane.cards.push({
        //   id: 'xxxxxxxx123', title: '', description: '', label: '',
        //   isEmpty: true,
        //   draggable: false,
        //   cardDraggable: false,
        // })
      }
    });
    return boardData;
  }
  //   TODO:// add dynamic board selection
  const data = currentBoard && currentBoard.lanes.length ? formatBoardData(currentBoard) : { lanes: [] };
  const translate = {
    'Add another lane': '+ Add new stack',
    'Click to add card': '+',
    'placeholder.title':'Enter Title',
    'button.Add lane' :'Add Stack',
    'button.Cancel': 'Cancel',

  };
  const customTitles = text => {
    return translate[text] ? translate[text] : text;
  };
  const onDataChange = newData => {
    onBoardDataChange(newData);
  };
  return (
    <div>
        <Board
          editable
          canAddLanes
          draggable
          collapsibleLanes
          data={data}
          className="boardContainer"
          laneStyle={{ backgroundColor: '#f5f5f5', borderRadius: '5px' }}
          components={{
            Card,
            LaneHeader,
            AddCardLink: CustomAddCardLink,
            NewLaneSection: CustomAddLaneButton
          }}
          t={customTitles}
          // onLaneAdd={onLaneAdd}
          onDataChange={onDataChange}
          eventBusHandle={setBoardEventBus}
        />
    </div>
  );
}
