import React, { useState, useContext, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import BackOfficeSidebar from './BackOfficeSidebar';
import BackLink from './BackLink';
import editicon from '../../../assets/edit_red.svg';
import { AppContext } from '../../../contexts/appContext';

export const ChecklistManagment: React.FC = (props) => {
  const {
    setCurrentBoardId,
    dispatchGetBoardDetails,
  } = useContext(AppContext);

  useEffect(() => {
    dispatchGetBoardDetails();
    setCurrentBoardId(props.match.params.boardid);
  },        []);

  const checklists = [
    {
      name: 'Policyholder Contacted & Inspection Scheduled',
      timer: '00d:10h:10m:10s',
    },
    {
      name: 'Arrived On site for Inspection',
      timer: '00d:10h:10m:10s',
    },
    {
      name: 'Pre-Mitigation Scan Uploaded to CORE Corp. MP Acct.',
      timer: '00d:10h:10m:10s',
    },
    {
      name: 'Post-Mitigation Scan Uploaded to CORE Corp. MP Acct.',
      timer: '00d:10h:10m:10s',
    },
    {
      name: 'Post-Repair Scan Uploaded to CORE Corp. MP Acct.',
      timer: '00d:10h:10m:10s',
    },
  ];

  return (
    <>
      <div className="row no-gutters">
        <div className="col-4 ">
          <BackOfficeSidebar></BackOfficeSidebar>
        </div>
        <div className="col-8 ">
          <BackLink></BackLink>
          <h1 className="backoffice_page_title">Checklist Management</h1>
          <table className="table table-stripes bktables ">
            <thead>
              <tr>
                <th>Checklist</th>
                <th>Timer</th>
                <th><img src={editicon} alt="" /></th>
              </tr>
            </thead>
            <tbody>
              {checklists.length
                ? checklists.map(list => {
                  const [editing, setEditing] = useState(false);
                  return (
                    <tr>
                    {
                      editing ? (
                        <>
                          <td><input type="text" defaultValue={list.name} /></td>
                          <td><input type="text" defaultValue={list.timer} /></td>
                          <td></td>
                        </>
                      ) : (
                        <>
                          <td>{list.name}</td>
                          <td>{list.timer}</td>
                          <td></td>
                        </>
                      )
                    }
                    </tr>
                  );
                })
                : ''}
            </tbody>
          </table>

          {/* <div className="row no-gutters">
            <div className="col">
              <div className="section_title save_btns">
                <button className="btn">Save</button>
                <button className="btn btn-transparent">back</button>
              </div>
            </div>
          </div> */}
        </div>
      </div>
    </>
  );
};

export default withRouter(ChecklistManagment);
