import React, { useState, useContext } from "react";
import Logo from "../../assets/main_logo.svg";
import notification from "../../assets/notification.svg";
import profile from "../../assets/profile.svg";
import account from "../../assets/account.svg";
import contactssupport from "../../assets/contactsupport.svg";
import logoutimg from "../../assets/logout.svg";
import { Dropdown, FormControl, Button, Modal } from "react-bootstrap";
import { AuthContext } from "../../contexts/authContext";
import NotifyDropdown from "./NotifyDropdown";
import history from "../../utils/history";
import kanbanwhite from "../../assets/home.svg";

import { AppContext } from "../../contexts/appContext";

const ContactSupportModal = props => {
  return (
    <>
      <Modal
        show={props.show}
        onHide={props.handleClose}
        className="contect_support_model"
      >
        <Modal.Header closeButton>
          <Modal.Title className="contact_support_model_title">
            Contact Support
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="support_body">
          <div className="form-group">
            <input type="text" placeholder="Name" />
          </div>
          <div className="form-group">
            <input type="email" placeholder="Email" />
          </div>
          <div className="form-group">
            <textarea placeholder="Write Message"></textarea>
          </div>
          <Button variant="primary" className="btn send_support">
            Send
          </Button>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default function Header() {
  const user = localStorage.profile ? JSON.parse(localStorage.profile) : false;
  const { logout } = useContext(AuthContext);
  const [supportShow, setsupportShow] = useState(false);
  const { currentBoard, isShowBoardLink } = useContext(AppContext);
  const [dropdownOpenAlerts, setDropdownOpenAlerts] = useState(false);
  const [dropdownOpenProfile, setDropdownOpenProfile] = useState(false);

  const handleSupportClose = () => setsupportShow(false);
  const handleSupportShow = () => setsupportShow(true);
  const goHome = () => {
    history.push("/dashboard/");
  };

  return (
    <div className="header">
      <div className="row no-gutters">
        <div className="main_logo col">
          <img src={Logo} alt="" />
          {isShowBoardLink ? (
            <div className="board_link_header" onClick={goHome}>
              <img src={kanbanwhite} alt="" /> Boards
            </div>
          ) : (
            ""
          )}
        </div>
        <div className="main_title col align-self-center">
          {isShowBoardLink && currentBoard ? currentBoard.name : " "}
        </div>
        <div className="main_action col text-right">
          <ul className="header_actions">
            <li onClick={() => {
              setDropdownOpenAlerts(!dropdownOpenAlerts);
            }}>
              <Dropdown  show={dropdownOpenAlerts} >
                <Dropdown.Toggle as={AlertCustomToggle} id="dropdown-alerts">
                  <i className="notify"></i>
                  <img src={notification} alt="" />
                </Dropdown.Toggle>

                <Dropdown.Menu
                  as={AlertMenu}
                  alignRight="true"
                  className="alert_dropdown_menu"
                >
                  <NotifyDropdown></NotifyDropdown>
                </Dropdown.Menu>
              </Dropdown>
            </li>
            <li onClick={() => {
              setDropdownOpenProfile(!dropdownOpenProfile);
            }}>
              <Dropdown  show={dropdownOpenProfile} >
                <Dropdown.Toggle as={CustomToggle} id="dropdown-basic">
                  <i className="notify"></i>
                  <img src={profile} alt="" />
                </Dropdown.Toggle>

                <Dropdown.Menu
                  as={CustomMenu}
                  alignRight="true"
                  className="main_dropdown_menu"
                >
                  <li className="full">{user ? user.email : ""}</li>
                  <li>
                    <Dropdown.Item eventKey="1" href="#">
                      <img src={account} alt="" /> Account
                    </Dropdown.Item>
                  </li>
                  <li>
                    <Dropdown.Item
                      eventKey="2"
                      href="#"
                      onClick={handleSupportShow}
                    >
                      <img src={contactssupport} alt="" /> Contact Support
                    </Dropdown.Item>
                  </li>
                  <li>
                    <Dropdown.Item eventKey="3" onClick={logout} href="#">
                      <img src={logoutimg} alt="" /> Logout
                    </Dropdown.Item>
                  </li>
                </Dropdown.Menu>
              </Dropdown>
            </li>
          </ul>
        </div>
      </div>
      <ContactSupportModal
        handleClose={handleSupportClose}
        show={supportShow}
      ></ContactSupportModal>
    </div>
  );
}

const AlertCustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={e => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
  </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const CustomMenu = React.forwardRef(
  ({ children, style, className, "aria-labelledby": labeledBy }, ref) => {
    const [value, setValue] = useState("");

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">
          {React.Children.toArray(children).filter(
            child =>
              !value || child.props.children.toLowerCase().startsWith(value)
          )}
        </ul>
      </div>
    );
  }
);

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={e => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
  </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const AlertMenu = React.forwardRef(
  ({ children, style, className, "aria-labelledby": labeledBy }, ref) => {
    const [value, setValue] = useState("");

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">
          {React.Children.toArray(children).filter(
            child =>
              !value || child.props.children.toLowerCase().startsWith(value)
          )}
        </ul>
      </div>
    );
  }
);
