import React, { useEffect } from "react";
import { withRouter, Link } from "react-router-dom";
import Header from "../../components/header";

import { AppContext } from "../../../contexts/appContext";
import { useContext } from "react";
import NoBoardFound from "app/components/NoBoardFound";
import kanbanwhite from "../../../assets/Kanban_white.svg";
import kanbanwhitebig from "../../../assets/Kanban_white_big.svg";
import signuprequestsicon from "../../../assets/SignUpRequestsWhite.svg";
import DashboardSidebar from "app/components/DashboardSidebar";

export const DashboardPage: React.FC = () => {
  const { dispatchGetBoardDetails, boards, setIsShowBoardLink, selectBoard, setDashboardSidebarActive } = useContext(AppContext);

  useEffect(() => {
    dispatchGetBoardDetails();
    setIsShowBoardLink(false);
    selectBoard(undefined);
    setDashboardSidebarActive('boards');
    return () => {
      setIsShowBoardLink(true);
    }
  }, []);



  return (
    <>
      <Header></Header>
      <div className="row">
        <div className="col-2 offset-1">
        <DashboardSidebar></DashboardSidebar>
        </div>
        <div className="col-7">
          <h1 className="backoffice_page_title">All Boards</h1>
          {boards.length
            ? boards.map((board, index) => {
                return (
                  <div className="board_item" key={index}>
                    <Link to={`/boarddetails/${board.id}`} className="">
                      <img src={kanbanwhitebig} alt="" />
                      <div className="board_title">{board.name}</div>
                    </Link>
                  </div>
                );
              })
            : ""}
        </div>
      </div>
    </>
  );
};

export default withRouter(DashboardPage);
