import * as React from 'react';
import notificationredicon from '../../assets/notificationRed.svg';

const ExpandedNotification = (prop) => {
    return (
        <div className="expanded_notification">
            <div className="expanded_noti_header">
                <div className="expanded_noti_header_title">
                    <img src={notificationredicon} alt="" />
                    {prop.noti.action}
                </div>
            </div>
            <div className="expanded_noti_body">
                <div className="notify_name">{prop.noti.title}</div>
                <div className="notify_description">{prop.noti.description}</div>
                <div className="notify_timestamp">{prop.noti.timestamp}</div>

            </div>
        </div>
    );
}

export default function NotifyDropdownItem(prop) {
    const notifications = prop.notifications;
    const expanded = prop.expanded;

    const allNoties = notifications.map((noti, index) => {
        const classes = ['notify_item'];
        if(noti.read){
            classes.push('read');
        }else{
            classes.push('unread');
        }

        if(expanded){
            classes.push('expanded');
        }else{
            classes.push('unexpanded');
        }

        return (
            <div className={classes.join(" ")} key={index}>
                {expanded ? (
                    <>
                    <ExpandedNotification noti={noti}></ExpandedNotification>
                    </>
                ) : (
                    <>
                        <div className="notify_name">{noti.title}</div>
                        <div className="notify_description">{noti.description}</div>
                        <div className="notify_timestamp">{noti.timestamp}</div>
                    </>
                )}
            </div>
        );
    });
    return (
        <>
            {allNoties}
        </>
    );
}
