const Links: React.FC<LinkProps & {
  isExternal?: boolean;
  className?: string;

}> = ({ to, children, isExternal, className }) => {
  return (
      <RouterLink to={to} className={className}>
        {children}
      </RouterLink>
  );
};
export default Links;