import * as React from 'react';
import NotifyDropdownItem from './NotifyDropdownItem';



const dummyNoti = [{
    title: 'John Potter',
    action: 'Card Moved',
    description: 'Lorem Ipsum is simply dummy text of typesetting.',
    timestamp: 'Jan 1,  12:05 PM',
    read: false,
},
{
    title: 'Roger David',
    action: 'Card Moved',
    description: 'Lorem Ipsum is simply dummy text of typesetting.',
    timestamp: 'Jan 1,  12:05 PM',
    read: false,
},
{
    title: 'Adam',
    action: 'Card Moved',
    description: 'Lorem Ipsum is simply dummy text of typesetting.',
    timestamp: 'Jan 1,  12:05 PM',
    read: true,
},
{
    title: 'Jack',
    action: 'Card Moved',
    description: 'Lorem Ipsum is simply dummy text of typesetting.',
    timestamp: 'Jan 1,  12:05 PM',
    read: true,
}];



export default function NotifyDropdown() {
    const [notifications, setNotification] = React.useState(dummyNoti);
    const [expanded, setExpanded] = React.useState(false);

    const markAllRead = () => {
        setNotification(notifications.map((noti) => {
            noti.read = true;
            return noti;
        }));
    }

    const changeExpand = () => {
        setExpanded(!expanded);
    }
    return (
        <>
            <li className="full notify_header">
                <span className="notify_title">Notifications</span>
                <span className="allreadbtn" onClick={markAllRead}>mark all as read</span>  
            </li>
            <li className="text_left">
    <span className="expand_link" onClick={changeExpand}>{expanded ? 'Collapse' : 'Expand'} Notifications</span>
            </li>
            <li className="pt10">
                <NotifyDropdownItem notifications={notifications} expanded={expanded} ></NotifyDropdownItem>
            </li>
        </>
    );
}

