import React, { useEffect, useContext, useState } from "react";
import { withRouter, Link } from "react-router-dom";
import BackOfficeSidebar from "./BackOfficeSidebar";
import { BackLink } from "./BackLink";
import { BackofficeContext } from "../../../contexts/backofficeContext";
import { AppContext } from "../../../contexts/appContext";
import history from '../../../utils/history';
export const UserManagement: React.FC = (props) => {
  const { dispatchGetUsers, users } = useContext(BackofficeContext);
  const { currentBoard,setCurrentBoardId,
    dispatchGetBoardDetails, } = useContext(AppContext);
  const [headers, setHeaders] = useState([]);


  useEffect(() => {
    dispatchGetBoardDetails();
    setCurrentBoardId(props.match.params.boardid);
  },        []);
  

  useEffect(() => {
    dispatchGetUsers();
  }, []);

  useEffect(() => {
    if (users.length) {
      setHeaders(Object.keys(users[0]));
      console.log(headers);
    }
  }, [users]);


  const parseValue = {
    roles: (roles) => {
      return roles[0].name;
    },
    isString: (value) => {
      return value
    }
  }



  const formatValue = (arr, key) => {
    if(key==="roles"){
      return parseValue.roles(arr[key]);
    }else{
      return parseValue.isString(arr[key]);
    }
  };


  const goToUserPage = (user) => {
    history.push(`/admin/${currentBoard.id}/useroverview/${user.name}`);
  }

  return (
    <>
      <div className="row no-gutters">
        <div className="col-4 ">
          <BackOfficeSidebar></BackOfficeSidebar>
        </div>
        <div className="col-8 ">
          <BackLink></BackLink>
          <h1 className="backoffice_page_title">User Management</h1>
          {
            headers.length ? (
          
          <table className="table table-stripes bktables ">
            <thead>
              <tr>
                {headers.length
                  ? headers.map((key, index) => (<th key={index}>{key}</th>))
                  : ""}
              </tr>
            </thead>
            <tbody>
              {headers.length
                ? users.map((user, uindex) => {
                    return (
                      <tr key={uindex}>
                        {Object.keys(user).map((key, index) => (<td key={index} onClick={() => {goToUserPage(user);}}>
                          {formatValue(user, key)}
                        </td>))}
                      </tr>
                    );
                  })
                : (<tr><td></td></tr>)}
            </tbody>
          </table>
            ) :  ' ' 
          } 
        </div>
      </div>
    </>
  );
};

export default withRouter(UserManagement);
