import React, { Component, useState } from "react";
import kanban from "../../assets/Kanban.svg";
import menubanner from "../../assets/menubanner.svg";
import searchicon from "../../assets/search.svg";
import editicon from "../../assets/edit.svg";
import deleteicon from "../../assets/delete.svg";
import kanbanicon from "../../assets/kanban-small.svg";
import formicon from "../../assets/formicon.svg";
import { Dropdown, Modal, Button } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import Links from "./Links";
import { AppContext } from "../../contexts/appContext";
import { useContext } from "react";
import history from "../../utils/history";
import { Link } from "react-router-dom";

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={e => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
  </a>
));

// forwardRef again here!
// Dropdown needs access to the DOM of the Menu to measure it
const CustomMenu = React.forwardRef(
  ({ children, style, className, "aria-labelledby": labeledBy }, ref) => {
    const [value, setValue] = useState("");

    return (
      <div
        ref={ref}
        style={style}
        className={className}
        aria-labelledby={labeledBy}
      >
        <ul className="list-unstyled">
          {React.Children.toArray(children).filter(
            child =>
              !value || child.props.children.toLowerCase().startsWith(value)
          )}
        </ul>
      </div>
    );
  }
);

const DesktopSearch = () => {
  return (
    <div className="input-group search_board">
      <input
        type="text"
        className="form-control"
        placeholder="Search"
        required
      />
      <img className="input_icon" src={searchicon} alt="" />
    </div>
  );
};

const MobileSearch = () => {
  return (
    <>
      <Dropdown>
        <Dropdown.Toggle as={CustomToggle} id="kanban-swicth-alerts">
          <div className="input-group mobile_search_board">
            <img className="input_icon" src={searchicon} alt="" />
          </div>
        </Dropdown.Toggle>

        <Dropdown.Menu
          as={CustomMenu}
          alignRight="true"
          className="board_search_dropdown_menu"
        >
          <li>
            <input
              type="text"
              className="form-control"
              placeholder="Search"
              required
            />
          </li>
        </Dropdown.Menu>
      </Dropdown>
    </>
  );
};

const RenameBoard = props => {
  return (
    <>
      <Modal
        show={props.show}
        onHide={props.handleClose}
        className="contect_support_model"
      >
        <Modal.Header closeButton>
          <Modal.Title className="contact_support_model_title">
            Rename Board
          </Modal.Title>
        </Modal.Header>
        <Modal.Body className="support_body">
          <form onSubmit={props.onStackSubmit}>
            <div className="form-group">
              <input
                type="text"
                placeholder="Name"
                value={props.value}
                onChange={props.onChange}
                required
              />
            </div>
            <Button
              variant="primary"
              className="btn send_support"
              type="submit"
            >
              Change
            </Button>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default function BoardHeader(props) {
  const { currentBoard, boards, selectBoard, renameBoardName } = useContext(
    AppContext
  );
  const [boardRenameShow, setBoardRenameShow] = useState(false);
  const [currentBoardName, setCurrentBoardName] = useState(currentBoard.name);

  const isHideRightSection = props.isHideRightSection ? true : false;

  const handleBoardClose = () => setBoardRenameShow(false);
  const handleBoardShow = () => setBoardRenameShow(true);

  const onChange = e => {
    const value = e.currentTarget.value;
    setCurrentBoardName(value);
  };

  const onBoardRenameSubmit = e => {
    e.preventDefault();
    renameBoardName({
      name: currentBoardName,
      id: currentBoard.id
    });
    handleBoardClose();
  };
  return (
    <div className="board_header ">
      <div className="row no-gutters align-items-center">
        <div className="board_icon col-4 align-self-center">
          <Dropdown>
            <Dropdown.Toggle as={CustomToggle} id="kanban-swicth-alerts">
              {props.view === "forms" ? (
                <img src={formicon} alt="" />
              ) : (
                <img src={kanban} alt="" />
              )}
              {currentBoard ? currentBoard.name : ""}
            </Dropdown.Toggle>

            <Dropdown.Menu
              as={CustomMenu}
              alignRight="true"
              className="kanban_swicth_dropdown_menu"
            >
              {/* {
                boards.length ? boards.map((board,index) => {
                  return (
                    <li key={index}>
                      <Dropdown.Item eventKey="1" onClick={() => {
                        history.push(`/boarddetails/${board.id}`);
                      }}>
                        {board.name}
                        </Dropdown.Item>
                    </li>
                  )
                }) : ''
              } */}
              <li>
                <Dropdown.Item
                  eventKey="1"
                  tag={Link}
                  onClick={() => {
                    history.push(`/boarddetails/${currentBoard.id}/`);
                  }}
                >
                  <img src={kanbanicon} alt="" /> Kanban
                </Dropdown.Item>
              </li>

              <li>
                <Dropdown.Item
                  eventKey="1"
                  tag={Link}
                  onClick={() => {
                    history.push(`/boarddetails/${currentBoard.id}/forms`);
                  }}
                >
                  <img src={formicon} alt="" /> Form
                </Dropdown.Item>
              </li>

              <li>
                <Dropdown.Item
                  eventKey="1"
                  tag={Link}
                  onClick={() => {
                    history.push(`/e/${window.btoa(currentBoard.id)}`);
                  }}
                >
                  <img src={formicon} alt="" /> Public Form
                </Dropdown.Item>
              </li>
            </Dropdown.Menu>
          </Dropdown>
        </div>
        {!isHideRightSection ? (
          <div className="main_action col-8 text-right">
            <div className="board_actions_row">
              <Dropdown>
                <Dropdown.Toggle as={CustomToggle} id="dropdown-alerts">
                  <img src={menubanner} className="action" alt="" />
                </Dropdown.Toggle>

                <Dropdown.Menu
                  as={CustomMenu}
                  alignRight="true"
                  className="board_dropdown_menu"
                >
                  <li onClick={handleBoardShow}>
                    <Dropdown.Item eventKey="1" href="#">
                      <img src={editicon} alt="" /> Rename View
                    </Dropdown.Item>
                  </li>
                  {/* <li><Dropdown.Item eventKey="2" href="#"><img src={deleteicon} alt="" /> Delete View</Dropdown.Item></li> */}
                </Dropdown.Menu>
              </Dropdown>
              <Links
                to={`/admin/${currentBoard.id}/usermanagement/`}
                className="btn backoffice"
              >
                Admin
              </Links>
              {/* <button className="btn backoffice"></button> */}
              <button className="btn invite">Invite</button>
              <ul className="usersAdded">
                <li>AH</li>
                <li>OS</li>
              </ul>
              <span className="divider"></span>
              {isMobile ? (
                <MobileSearch></MobileSearch>
              ) : (
                <DesktopSearch></DesktopSearch>
              )}
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
      <RenameBoard
        value={currentBoardName}
        onChange={onChange}
        onStackSubmit={onBoardRenameSubmit}
        show={boardRenameShow}
        handleClose={handleBoardClose}
      ></RenameBoard>
    </div>
  );
}
