import React, { useContext, useEffect, useState } from "react";
import { withRouter } from "react-router-dom";
import { AuthContext } from "contexts/authContext";
import publicLogo from "../../assets/public_logo.svg";
import calendaricon from "../../assets/calendar_input.svg";
import fileicon from "../../assets/file.svg";
import formFields from "./fields.json";
import CORECheckbox from "app/components/CORECheckbox";
import { useForm } from "react-hook-form";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng
} from "react-places-autocomplete";
import { claimForm } from "utils/api-routes/api-routes.util";
import { AppAlertsContext } from "contexts/appAlertsContext";
import DatePicker from "react-datepicker";

export const FormPublicView: React.FC = props => {
  const [fields, setFields] = useState(formFields);
  const { alertDetails: alertDetails, showAlert, hideAlert } = useContext(
    AppAlertsContext
  );
  const { handleSubmit, register, errors } = useForm();
  const [formSubmitted, setFormSubmitted] = useState(false);
  const boardId = window.atob(props.match.params.boardid);
  const onSubmit = e => {
    e.preventDefault();
    const apiData = {
      boardId: boardId,
      cardTitle: "",
      description: "",
      otherData: {},
      label: "",
      filesToUpload: ""
    };
    const formData = new FormData();
    fields.map(field => {
      if (field.type === "file") {
        apiData.filesToUpload = field.value;
      } else {
        apiData.otherData[field.name] = field.value;
        if (field.name === "PH First Name") {
          apiData.cardTitle = field.value;
        }
      }
    });
    formData.append("filesToUpload", apiData.filesToUpload);
    // formData.append("boardId", "3");
    // formData.append("cardTitle", apiData.cardTitle);
    // formData.append("description", apiData.description);
    // formData.append("otherData", JSON.stringify(apiData.otherData));
    // formData.append("label", apiData.label);

    claimForm(formData, apiData).subscribe(
      (response: any) => {
        if (response.response.Requested_Action) {
          // dispatchGetBoardDetails();

          showAlert({
            alertType: "success",
            message: "Your Form has been successfully submitted",
            isTimely: false
          });
          setFormSubmitted(true);
        } else {
          showAlert({
            alertType: "error",
            message: "something wrong.",
            isTimely: false
          });
        }
        // //console.log(response);
      },
      response => {
        //console.log(response);
      }
    );
  };

  const onChanges = (name, value) => {
    const allFields = Object.assign([], fields);
    allFields.map(field => {
      if (field.name === name) {
        field["value"] = value;
      }
      return field;
    });
    setFields(allFields);
  };

  const breakpoints = { 350: 1, 750: 1, 900: 1 };
  return (
    <>
      <header className="form_public_header"></header>
      <div className="form_public_container">
        <div className="public_logo">
          <img src={publicLogo} alt="" />
        </div>
        <h1 className="form_public_title">Claim Submission Form</h1>
        {formSubmitted ? (
          <div className="form_submitted">
            Claim details has been submitted successfully.
          </div>
        ) : (
          <form className="form_public" onSubmit={onSubmit}>
            {fields.length
              ? fields.map(field => {
                  const input = type => {
                    switch (type) {
                      case "text":
                        return (
                          <input
                            type="text"
                            className="form-control"
                            required={field.required}
                            onChange={e => {
                              onChanges(field.name, e.currentTarget.value);
                            }}
                          />
                        );
                        break;

                      case "email":
                        return (
                          <>
                            <input
                              type="email"
                              className="form-control"
                              required={field.required}
                              onChange={e => {
                                onChanges(field.name, e.currentTarget.value);
                              }}
                              ref={register({
                                required: field.required,
                                pattern: {
                                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                                  message: "invalid email address"
                                }
                              })}
                            />
                            {errors.email && errors.email.message}
                          </>
                        );
                        break;

                      case "price":
                        return (
                          <input
                            type="number"
                            className="form-control"
                            min="1"
                            step="any"
                            onChange={e => {
                              onChanges(field.name, e.currentTarget.value);
                            }}
                          />
                        );
                        break;

                      case "textarea":
                        return (
                          <textarea
                            className="form-control textarea"
                            onChange={e => {
                              onChanges(field.name, e.currentTarget.value);
                            }}
                          ></textarea>
                        );
                        break;

                      case "address":
                        return (
                          <PlacesAutocomplete
                            value={field.value}
                            onChange={address => {
                              onChanges(field.name, address);
                            }}
                            onSelect={address => {
                              onChanges(field.name, address);
                            }}
                          >
                            {({
                              getInputProps,
                              suggestions,
                              getSuggestionItemProps,
                              loading
                            }) => (
                              <div>
                                <input
                                  {...getInputProps({
                                    placeholder: "Search Places ...",
                                    className: "form-control"
                                  })}
                                />
                                <div className="address_dropdown">
                                  {loading && <div>Loading...</div>}
                                  {suggestions.map(suggestion => {
                                    const className = suggestion.active
                                      ? "suggestion-item active"
                                      : "suggestion-item";
                                    // inline style for demonstration purpose
                                    const style = suggestion.active
                                      ? {
                                          backgroundColor: "#fafafa",
                                          cursor: "pointer"
                                        }
                                      : {
                                          backgroundColor: "#ffffff",
                                          cursor: "pointer"
                                        };
                                    return (
                                      <div
                                        {...getSuggestionItemProps(suggestion, {
                                          className,
                                          style
                                        })}
                                      >
                                        <span>{suggestion.description}</span>
                                      </div>
                                    );
                                  })}
                                </div>
                              </div>
                            )}
                          </PlacesAutocomplete>
                        );
                        break;

                      case "file":
                        return (
                          <>
                            <label
                              htmlFor="fileup"
                              className="file_upload_label"
                            >
                              <input
                                id="fileup"
                                type="file"
                                className="form-control file"
                                multiple
                                accept=".pdf,.esx,.skx,.jpeg,.png,.xls,.xlsx,.doc,.docx,.rtf,.xml,.dwg,.zip"
                                onChange={e => {
                                  onChanges(field.name, e.target.files);
                                }}
                              />
                              <img src={fileicon} alt="" /> Attach File
                            </label>
                            <div className="file_previews">
                              <ul>
                                {field.value
                                  ? Object.values(field.value).map(file => {
                                      console.log(file);
                                      return (
                                        <>
                                          <li>{file.name}</li>
                                        </>
                                      );
                                    })
                                  : ""}
                              </ul>
                            </div>
                          </>
                        );
                        break;

                      case "date":
                        return (
                          <>
                            <div className="input-group">
                              <DatePicker
                                selected={field.value}
                                onChange={(date) => {
                                  onChanges(field.name, date);
                                }}
                                dateFormat="MMM do, yyyy"
                              />
                              <span className="date_input_icon">
                                <img src={calendaricon} alt="" />
                              </span>
                            </div>
                          </>
                        );
                        break;

                      case "checkbox":
                        return (
                          <CORECheckbox
                            options={field.options}
                            name={field.name}
                            callback={onChanges}
                          ></CORECheckbox>
                        );
                        break;
                      default:
                        return "";
                        break;
                    }
                  };
                  return (
                    <div className="form-group">
                      <label className="heading">
                        {field.label}{" "}
                        <span className="red">{field.required ? "*" : ""}</span>
                      </label>
                      <div className="sub_heading">{field.subHeading}</div>
                      {input(field.type)}
                    </div>
                  );
                })
              : ""}
            <button className="btn form_public_submit" type="submit">
              Let CORE take it from here
            </button>
            {/* <button className="btn btn-transparent form_public_share_btn">
              Share Link
            </button> */}
          </form>
        )}
      </div>
    </>
  );
};

export default withRouter(FormPublicView);
