import React, { useContext } from "react";
import kanbanwhite from "../../assets/Kanban_white.svg";
import signuprequestsicon from "../../assets/SignUpRequestsWhite.svg";
import { AppContext } from "contexts/appContext";
import { Link } from "react-router-dom";
const DashboardSidebar = () => {
  const { dashboardActive } = useContext(AppContext);

  return (
    <>
      <Link to="/dashboard/" className="dashboard_sidebar_link">
        <div className={`board_link active_${dashboardActive.boards}`}>
          <img src={kanbanwhite} alt="" /> Boards
        </div>
      </Link>
      <Link to="/signuprequests/" className="dashboard_sidebar_link">
        <div
          className={`board_link signuplink active_${dashboardActive.signup}`}
        >
          <img src={signuprequestsicon} alt="" /> Sign Up Requests
        </div>
      </Link>
    </>
  );
};

export default DashboardSidebar;
